import { motion } from 'framer-motion';
import React from 'react';
import styled from 'styled-components';
import logo from '../assets/img/logo-svg.svg';
import search from '../assets/img/search.png';

const Section = styled.div`
    display: flex;
    justify-content: center;

    @media only screen and (max-width: 768px) {
        width: 100%;
    }
`;

const Container = styled.div`
    width: 1040px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 0px;

    @media only screen and (max-width: 1040px) {
        width: 991px;
    }

    @media only screen and (max-width: 768px) {
        width: 100%;
        padding: 20px;
    }
`;

const Links = styled.div`
    display: flex;
    align-items: center;
    gap: 50px;
`;

const Logo = styled(motion.img)`
    height: 50px;
    border-radius: 15px;
`;

const List = styled.ul`
    display: flex;
    gap: 20px;
    list-style: none;

    @media only screen and (max-width: 768px) {
        display: none;
    }
`;

const ListItem = styled.a`
    cursor: pointer;
    text-decoration: none;
    color: cyan;
`;

const Icons = styled.div`
    display: flex;
    align-items: center;
    gap: 20px;
`;
const Icon = styled.img`
    height: 18px;
    width: 18px;
    cursor: pointer;
`;
const Button = styled.a`
    padding: 5px 10px;
    background-color: #08a858;
    color: #fff;
    border: none;
    border-radius: 5px;
    text-decoration: none;
    cursor: pointer;
`;

const Navbar = () => {
    return (
        <Section>
            <Container>
                <Links>
                    <Logo
                        src={logo}
                        style={{ cursor: 'pointer' }}
                        alt="logo"
                        animate={{ rotate: 360 }}
                        transition={{
                            delay: 2,
                            duration: 3,
                            repeat: Infinity,
                            repeatType: 'reverse',
                            type: 'spring',
                        }}
                    />
                    <List>
                        <ListItem>Home</ListItem>
                        <ListItem>Studio</ListItem>
                        <ListItem
                            target={'blank'}
                            href="https://imil1.github.io/portfolio/#/projects">
                            Works
                        </ListItem>
                        <ListItem>Contact</ListItem>
                    </List>
                </Links>
                <Icons>
                    <Icon
                        src={search}
                        alt="search"
                    />
                    <Button
                        target={'blank'}
                        href="https://github.com/imil1?tab=repositories">
                        Github rep
                    </Button>
                </Icons>
            </Container>
        </Section>
    );
};

export default Navbar;
