import React from 'react';
import styled from 'styled-components';
import { Canvas } from '@react-three/fiber';
import { OrbitControls, MeshDistortMaterial, Sphere } from '@react-three/drei';
import { motion } from 'framer-motion';

import Navbar from './Navbar';
import line from '../assets/img/line.png';
import avtobot from '../assets/img/avtobot.png';

const Section = styled(motion.div)`
    height: 100vh;
    scroll-snap-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;

    @media only screen and (max-width: 768px) {
        height: 200vh;
    }
`;
const Container = styled.div`
    height: 100%;
    scroll-snap-align: center;
    max-width: 1040px;
    width: 100%;
    display: flex;
    justify-content: space-between;

    @media only screen and (max-width: 1040px) {
        max-width: 991px;
        width: 100%;
        /* padding: 0 50px; */
    }

    @media only screen and (max-width: 768px) {
        width: 100%;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        /* padding: 50px; */
    }
`;
const Left = styled.div`
    flex: 2;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 20px;

    @media only screen and (max-width: 768px) {
        flex: 1;
        align-items: center;
    }
`;
const Title = styled(motion.h1)`
    font-size: 64px;

    @media only screen and (max-width: 768px) {
        text-align: center;
    }

    @media only screen and (max-width: 450px) {
        font-size: 42px;
    }
`;
const WhatWeDo = styled(motion.div)`
    display: flex;
    align-items: center;
    gap: 15px;
`;
const Line = styled.img`
    height: 5px;
    width: 20px;
`;
const Subtitle = styled.h2`
    text-transform: capitalize;
    color: #08a858;
`;
const Desc = styled(motion.p)`
    text-transform: capitalize;
    font-size: 24px;
    color: lightcyan;

    @media only screen and (max-width: 768px) {
        padding: 20px;
        text-align: center;
        max-width: 250px;
        width: 100%;
        overflow: hidden;
        display: -webkit-box;
        -webkit-line-clamp: 4;
        -webkit-box-orient: vertical;
    }

    @media only screen and (max-width: 450px) {
        max-width: 250px;
        width: 100%;

        overflow: hidden;
        display: -webkit-box;
        -webkit-line-clamp: 3;
        -webkit-box-orient: vertical;
    }
`;
const Button = styled(motion.a)`
    background-color: #08a858;
    color: #fff;
    text-transform: capitalize;
    font-size: 16px;
    font-weight: 500;
    max-width: 150px;
    width: 100%;
    padding: 10px 15px;
    border: none;
    text-align: center;
    text-decoration: none;
    border-radius: 13px;

    cursor: pointer;
    transform: translateY(15px);
`;
const Right = styled.div`
    flex: 3;
    position: relative;
    @media only screen and (max-width: 768px) {
        flex: 1;
        width: 100%;
    }
`;
const Img = styled.img`
    max-width: 800px;
    width: 100%;
    height: 300px;
    object-fit: contain;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;

    animation: animate 2s infinite ease alternate;

    @media only screen and (max-width: 768px) {
        width: 150px;
        height: 150px;
    }
    @media only screen and (max-width: 450px) {
        width: 100px;
        height: 100px;
    }

    @keyframes animate {
        100% {
            transform: translateY(-30px);
        }
    }
`;

const textAnim = {
    hidden: {
        opacity: 0,
    },
    visible: (custom) => ({
        opacity: 1,

        transition: { delay: custom * 0.3 },
    }),
};

const Hero = React.memo(() => {
    return (
        <Section
            initial="hidden"
            whileInView="visible"
            viewport={{ amount: 0.2, once: true }}>
            <Navbar />
            <Container>
                <Left>
                    <Title
                        variants={textAnim}
                        custom={1}
                        transition={{
                            type: 'tween',
                            stiffness: 100,
                        }}>
                        React. Three. motion.
                    </Title>
                    <WhatWeDo
                        variants={textAnim}
                        custom={2}
                        transition={{
                            type: 'tween',
                            stiffness: 100,
                        }}>
                        <Line src={line} />
                        <Subtitle>in one</Subtitle>
                    </WhatWeDo>
                    <Desc
                        variants={textAnim}
                        custom={2}
                        transition={{
                            type: 'tween',
                            stiffness: 100,
                        }}>
                        Single Page Application (SPA) is a web application that
                        does not use the default method of loading new pages
                        completely.
                    </Desc>
                    <Button
                        variants={textAnim}
                        custom={2}
                        transition={{
                            type: 'tween',
                            stiffness: 100,
                        }}
                        target={'blank'}
                        href="https://www.framer.com/motion/">
                        learn more
                    </Button>
                </Left>
                <Right>
                    <Canvas>
                        <OrbitControls enableZoom={false} />
                        <ambientLight intensity={1} />
                        <directionalLight position={[6, 5, 4]} />
                        <Sphere
                            args={[1, 50, 150]}
                            scale={1.5}
                            >
                            <MeshDistortMaterial
                                color="#0b071d"
                                attach="material"
                                distort={0.5}
                                speed={3}
                                
                            />
                        </Sphere>
                    </Canvas>
                    <Img
                        src={avtobot}
                        
                    />
                </Right>
            </Container>
        </Section>
    );
});

export default Hero;
